* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 4.5rem;
}

input {
  font-size: 1.2rem;
  width: 100%;
  color: gray;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card img {
  margin-left: 10px;
  margin-top: 10px;
}

.card p {
  text-align: center;
}